*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}
section{
    height:100vh;
    width:100%;
    background-color: aliceblue;
    display: flex;
    align-items: center;
    justify-content: center;
}
.container{
    text-align: center;
    width:100%;
    max-width:500px;
    height:90vh;
    padding:20px;
    margin:0 auto;
    box-shadow: 0px 0px 20px #00000010 ;
    justify-content: center;
    background-color: white;
    border-radius: 12px;
    margin-bottom: 20px;
}
.form-group 
{
    margin-top: 20px;
    width:100%;
    font-size: 20px;
}

.form-group input{
    text-align: left;
    margin-top: 5px;
    font-size:18px;
    padding:5px;
    height:50%;
    width:100%;
    border-style: 1px solid;
}
.form-group textarea{
    text-align: left ;
    margin-top: 5px;
    margin-left: 2px;
    font-size:18px;
    padding:5px;
    height:50%;
    width:100%;
    border-style: 1px solid;
    
}
.form-group label{
    align-items: center;
    
}
textarea{
    resize: vertical;

}
.form-group-button{
    width:100%;
    border:none;
    outline:none;
    font-size: 24px;
    margin-top:5px;
    border-radius: 8px;
    padding: 20px;
    font-family: 'Montserrat';
    color:rgb(27,166,247);
    cursor:pointer;
    transition: .3s ease background-color;

}
.form-group-button:hover{
    color:rgb(214,226,236);

}
.confirmation-page {
    color:blue;
    text-align: center;
    justify-content: center;
    width:50%;
    max-width: 400px;
    margin : 0 auto;
    height:20vh;
    border-radius: 12px;
    margin-bottom: 20px;
    justify-content: center;
    padding-top: 2rem;
    padding-left: 1rem;
    
    

}
.confirmation-container{
    background-color:lightgray;
    width:100%;
    max-width:500px;
    height:auto;
    
    margin-left:30rem;
    margin-top: 5rem;
    
    border-radius: 8px;
    box-shadow: 0px, 0px, 20px #00000010;
    
}
