

.hero-container{
    background: url('/src/imagessrc/cowlogo-1.jpeg') center center/cover no-repeat;
    height:100vh;
    width:100%;
    direction:flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    box-shadow: inset 0 0 0 1000px rgba(0,0,0,0.2);
    object-fit: contain;
}
.hero-btns{
    margin-top: 30px;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 30px;
    
}
.hero-container > p{
    text-align: center;
    align-items: center;
    margin-top: 8px;
    font-size: 32px;
    color:#fff;
    padding: 15px;
    font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif;    

}
.hero-container > h1{
    padding:35px;
    color:#fff;
    font-size:100px;
    align-items: center;
    border-top: 20px;
    
}
.hero-btns .btn{
    margin:6px;
    align-items: center;
    justify-content: center;
    text-align: center;
   
    
}
.fa-play-circle{
    margin-left: 4px;
}
@media screen and (max-width: 960px){
    .hero-container > h1{
        font-size: 70px;
    }
}
@media screen and (max-width: 768px){
    .hero-container > h1{
        font-size: 50px;
    }
    .hero-container > p{
        font-size: 28px;
    }

.btn-mobile{
    display:block;
    text-decoration: none;

}
.btn{
    width:100%;
}
}